<template>
    <v-row class="justify-center">
        <v-col cols="12" md="10">
            <v-alert
                v-model="alert"
                :type="alertType"
                border="left"
                close-text="Close Alert"
                dismissible
                transition="scale-transition"
            >
                {{ alertMessage }}
            </v-alert>

            <v-dialog v-model="dialogDetail" width="500">
                <v-card>
                    <v-card-title class="text-h5"> Totals</v-card-title>

                    <v-card-text class="pb-1">
                        Captured Amount:
                        {{ batchDetail.capturedAmount | currency }}
                    </v-card-text>

                    <v-card-text class="pb-1">
                        Terminal Transaction Count:
                        {{ batchDetail.transactionCount }}
                    </v-card-text>

                    <v-card-text
                        v-if="
                            batchDetail.transactionstotal_non_terminal_transactions !=
                            0
                        "
                        class="pb-1"
                    >
                        Non Terminal Transaction Count:
                        {{
                            batchDetail.transactionstotal_non_terminal_transactions
                        }}
                    </v-card-text>

                    <v-card-text class="pb-3">
                        Captured Deposit Amount:
                        {{ batchDetail.expectedDeposit | currency }}
                    </v-card-text>

                    <v-card-text
                        v-if="batchDetailVolumeByTerminalLength > 0"
                        class="text-h5 pb-2"
                    >
                        Per Register
                    </v-card-text>
                    <v-card-text
                        v-for="i in batchDetailVolumeByTerminalLength"
                        :key="i"
                        class="pb-1"
                    >
                        Terminal Name:
                        {{ batchDetail.volumeByTerminal[i - 1].terminalName }}
                        <br />
                        Captured Amount:
                        {{ batchDetail.volumeByTerminal[i - 1].capturedAmount }}
                        <br />
                        Transaction Count:
                        {{
                            batchDetail.volumeByTerminal[i - 1].transactionCount
                        }}
                        <br /><br />
                    </v-card-text>

                    <v-divider></v-divider>

                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                            color="primary"
                            text
                            @click="dialogDetail = false"
                        >
                            OK
                        </v-btn>
                    </v-card-actions>
                </v-card>
            </v-dialog>

            <v-data-table
                :footer-props="tableFooterProps"
                :headers="headers"
                :items="batches"
                :loading="loading"
                :search="search"
                class="elevation-4 rounded-lg"
                item-key="batchId"
                loading-text="Loading... Please wait"
                show-expand
                single-expand
                sort-by="openDate"
                sort-desc
                @item-expanded="getTransactionsForBatch"
            >
                >
                <template v-slot:[`item.capturedAmount`]="{ item }">
                    <div
                        :class="
                            item.capturedAmount < 0
                                ? 'red--text font-weight-bold'
                                : ''
                        "
                    >
                        {{ item.capturedAmount | currency }}
                    </div>
                </template>
                <template v-slot:[`item.open`]="{ item }">
                    {{ item.open ? 'Open' : 'Closed' }}
                </template>
                <template v-slot:[`item.openDate`]="{ item }">
                    {{ formatDateTime(item.openDate) }}
                </template>
                <template v-slot:[`item.closeDate`]="{ item }">
                    {{ formatDateTime(item.closeDate) }}
                </template>
                <template v-slot:[`item.details`]="{ item }">
                    <v-btn
                        class="white--text"
                        color="teal"
                        @click="batchDetails(item.batchId)"
                    >
                        Details
                    </v-btn>
                </template>
                <template v-slot:[`item.excelExport`]="{ item }">
                    <v-btn
                        :href="
                            getInternalCoreClearController +
                            '/excel_export_batch_history/' +
                            item.batchId
                        "
                        class="white--text"
                        color="green"
                    >
                        Excel Export
                    </v-btn>
                </template>

                <template v-slot:expanded-item="{ headers, item }">
                    <td :colspan="headers.length">
                        <v-data-table
                            :footer-props="tableFooterProps"
                            :headers="transactionHeaders"
                            :items="item.transactions"
                            :loading="transactionLoading"
                            class="elevation-0 ma-2"
                            dense
                            item-key="transactionId"
                            loading-text="Loading... Please wait"
                            sort-by="timestamp"
                            sort-desc
                        >
                            <template v-slot:[`item.timestamp`]="{ item }">
                                {{ formatDateTime(item.timestamp) }}
                            </template>
                            <template v-slot:[`item.sale_id`]="{ item }">
                                <template v-if="item.receipt_link != null">
                                    <v-btn
                                        class="white--text"
                                        color="primary"
                                        :href="item.receipt_link"
                                        target="_blank"
                                    >
                                        {{ item.sale_id }}
                                    </v-btn>
                                </template>
                                <template v-else-if="item.sale_id != null">
                                    {{ item.sale_id }}
                                </template>
                                <template v-else> N/A </template>
                            </template>
                            <template v-slot:[`item.approved`]="{ item }">
                                <bool-status
                                    :status="item.approved"
                                    :status-message="item.responseDescription"
                                />
                            </template>
                            <template
                                v-slot:[`item.authorizedAmount`]="{ item }"
                            >
                                <div
                                    :class="
                                        item.transactionType === 'void' ||
                                        item.transactionType === 'refund'
                                            ? 'red--text font-weight-bold'
                                            : ''
                                    "
                                >
                                    {{
                                        item.authorizedAmount
                                            | currency(item.transactionType)
                                    }}
                                </div>
                            </template>
                            <template
                                v-slot:[`item.transactionType`]="{ item }"
                            >
                                {{ item.transactionType | titleCase }}
                            </template>
                            <template v-slot:[`item.entryMethod`]="{ item }">
                                {{ item.entryMethod | titleCase }}
                            </template>
                            <template v-slot:[`item.paymentType`]="{ item }">
                                {{ item.paymentType | titleCase }}
                            </template>
                        </v-data-table>
                    </td>
                </template>

                <template v-slot:top>
                    <v-toolbar
                        class="rounded-t-lg"
                        color="green darken-2"
                        dark
                        flat
                    >
                        <v-toolbar-title>Batches</v-toolbar-title>
                        <v-divider class="mx-4" inset vertical />
                        <v-text-field
                            v-model="search"
                            append-icon="search"
                            clearable
                            dark
                            hide-details
                            label="Search"
                            single-line
                        />
                        <v-divider class="mx-4" inset vertical />
                        <v-menu
                            v-model="datePickerMenu"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            min-width="auto"
                            offset-y
                            transition="scale-transition"
                        >
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                    v-model="dateRangeText"
                                    append-icon="mdi-calendar"
                                    hide-details
                                    label="Date Range"
                                    readonly
                                    single-line
                                    v-bind="attrs"
                                    v-on="on"
                                />
                            </template>
                            <v-date-picker
                                v-model="dateRange"
                                range
                                @change="initialize"
                            />
                        </v-menu>
                    </v-toolbar>
                </template>
                <template v-slot:no-data>
                    <v-alert class="ma-2" prominent text type="info">
                        <v-row align="center">
                            <v-col class="grow"> No records found</v-col>
                            <v-col class="shrink">
                                <v-btn
                                    class="my-2 white--text"
                                    color="green"
                                    @click="initialize"
                                >
                                    Retry
                                </v-btn>
                            </v-col>
                        </v-row>
                    </v-alert>
                </template>
            </v-data-table>
        </v-col>
    </v-row>
</template>

<script>
import { mapGetters } from 'vuex';
import axios from 'axios';
import accounting from 'accounting';
import moment from 'moment';
import BoolStatus from '@/components/BoolStatus';

export default {
    name: 'BatchList',
    components: { BoolStatus },
    data() {
        return {
            dialogDetail: false,
            batchDetail: {},
            batchDetailVolumeByTerminalLength: 0,
            datePickerMenu: false,
            loading: false,
            transactionLoading: false,
            alert: false,
            alertMessage: '',
            alertType: 'success',
            search: '',
            filterType: null,
            tableFooterProps: {
                showFirstLastPage: true,
            },
            headers: [
                { text: 'Batch ID', align: 'left', value: 'batchId' },
                { text: 'Amount', align: 'right', value: 'capturedAmount' },
                { text: 'Status', align: 'left', value: 'open' },
                { text: 'Start Date', align: 'left', value: 'openDate' },
                { text: 'End Date', align: 'left', value: 'closeDate' },
                {
                    text: 'Details',
                    align: 'left',
                    value: 'details',
                    sortable: false,
                    filterable: false,
                },
                {
                    text: 'Excel Export',
                    align: 'left',
                    value: 'excelExport',
                    sortable: false,
                    filterable: false,
                },
            ],
            batches: [],
            transactionHeaders: [
                { text: 'Date', align: 'left', value: 'timestamp' },
                {
                    text: 'Sale ID',
                    align: 'left',
                    value: 'sale_id',
                },
                {
                    text: 'Transaction ID',
                    align: 'left',
                    value: 'transactionId',
                },
                { text: 'Approved', align: 'left', value: 'approved' },
                { text: 'Card Holder', align: 'left', value: 'cardHolder' },
                { text: 'Amount', align: 'right', value: 'authorizedAmount' },
                {
                    text: 'Transaction Type',
                    align: 'center',
                    value: 'transactionType',
                },
                { text: 'Entry Method', align: 'left', value: 'entryMethod' },
                { text: 'Payment Type', align: 'left', value: 'paymentType' },
                { text: 'Masked Card', align: 'left', value: 'maskedPan' },
            ],
            dateRange: [
                moment().subtract(30, 'days').toISOString(),
                moment().toISOString(),
            ],
        };
    },
    computed: {
        ...mapGetters(['getInternalCoreClearController']),
        dateRangeText() {
            return this.dateRange
                .map((date) => moment(date).format('MM/DD/YYYY'))
                .join(' through ');
        },
    },
    mounted() {
        this.initialize();
    },
    methods: {
        openAlert(type, message) {
            this.alertType = type;
            this.alertMessage = message;
            this.alert = true;
        },
        async initialize() {
            this.loading = true;
            this.datePickerMenu = false;

            const startDate =
                this.dateRange[0] !== undefined ? this.dateRange[0] : '';
            const endDate =
                this.dateRange[1] !== undefined ? this.dateRange[1] : '';
            try {
                let response = await axios.get(
                    `${this.getInternalCoreClearController + '/batches/1'}`,
                    {
                        withCredentials: true,
                        crossDomain: true,
                        params: {
                            start_date: moment(startDate).format('MM/DD/YYYY'),
                            end_date: moment(endDate).format('MM/DD/YYYY'),
                        },
                    }
                );

                this.loading = false;

                if (response.data.batches) {
                    this.batches = response.data.batches;
                } else {
                    console.log('No batches found');
                    this.batches = [];
                }
            } catch (error) {
                console.error('Could not get list of batches');
                console.error(error);

                this.openAlert('error', error.toString());

                this.loading = false;
                this.batches = [];
            }
        },
        async getTransactionsForBatch({ item, value }) {
            if (value) {
                this.transactionLoading = true;
                try {
                    let response = await axios.get(
                        `${
                            this.getInternalCoreClearController +
                            '/get_transactions_for_batch/1'
                        }`,
                        {
                            withCredentials: true,
                            crossDomain: true,
                            params: {
                                batch_id: item.batchId,
                            },
                        }
                    );

                    this.transactionLoading = false;

                    if (response.data) {
                        item.transactions = response.data;
                    } else {
                        console.log('No transactions found');
                    }
                } catch (error) {
                    console.error('Could not get list of transactions');
                    console.error(error);
                }
            }
        },
        formatDateTime(rawDateTime) {
            return rawDateTime
                ? moment(rawDateTime).format('MM/DD/YYYY LT')
                : '';
        },
        async batchDetails(batchId) {
            let response = await axios.get(
                `${this.getInternalCoreClearController + '/batch_details/1'}`,
                {
                    withCredentials: true,
                    crossDomain: true,
                    params: {
                        batch_id: batchId,
                    },
                }
            );

            if (response.data) {
                this.batchDetail = response.data;
                this.batchDetailVolumeByTerminalLength =
                    response.data.volumeByTerminal.length;

                var total_terminal_transactions = 0;
                if (response.data.volumeByTerminal.length) {
                    for (
                        var k = 0;
                        k < response.data.volumeByTerminal.length;
                        k++
                    ) {
                        var terminal = response.data.volumeByTerminal[k];
                        total_terminal_transactions +=
                            terminal.transactionCount;
                    }
                } else {
                    total_terminal_transactions =
                        response.data.transactionCount;
                }

                this.batchDetail.transactionstotal_non_terminal_transactions =
                    response.data.transactionCount -
                    total_terminal_transactions;
            } else {
                console.log('No details found');
            }

            this.dialogDetail = true;
        },
    },
    filters: {
        titleCase: (value) => {
            if (!value) return '';

            value = value.toString().toLowerCase().split(' ');

            for (let i = 0; i < value.length; i++) {
                value[i] = value[i][0].toUpperCase() + value[i].slice(1);
            }

            return value.join(' ');
        },
        currency: (value, transactionType) => {
            if (!value) return '0';

            let formattedValue = accounting.formatMoney(value.replace('-', ''));

            return value < 0 ||
                transactionType === 'void' ||
                transactionType === 'refund'
                ? '-' + formattedValue
                : formattedValue;
        },
    },
};
</script>
